@import '../utils/variables';

.container {
    margin-left: auto;
    margin-right: auto;

  @media screen and (min-width: $mobile) {
      width: $mobile;
  }

  @media screen and (min-width: $tablet) {
      width: $tablet;
  }

  @media screen and (min-width: $desktop) {
      width: $desktop;
  }
}